import {
  useFinanceSendAllStudentMessage,
  useFinanceSendMesageAcademicStudentList,
  useFinanceSendMesageStudentList,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FeesStructure/FeesStructure.module.css";
import {
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import PendingFeesSendReminderFilter from "../../../AdmissionManager/PendingFees/StudentReminder/PendingFeesSendReminderFilter";
import PendingFeesSendReminderStudentList from "../../../AdmissionManager/PendingFees/StudentReminder/PendingFeesSendReminderStudentList";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import FormFile from "../../../../Ui/Input/FormFile";
const AllStudentMessageSend = ({
  onClose,
  instituteId,
  headId,
  accessAs,
  financeId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(1);
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [sendNotify, setSendNotify] = useState({
    alarm_mode: "APP_NOTIFICATION",
    content: "",
    m_title: "",
    m_doc: "",
  });
  const [studentList, setStudentList] = useState([]);
  const [selectedId, setSelectedId] = useState([]);
  const [applyFilter, setApplyFilter] = useState({
    depart: "",
    batch: "",
    master: "",
    batch_status: "",
    all_depart: "",
    category: "",
    departmentType: "",
  });
  const [file, setFile] = useState("");

  const [fileUpload] = useFileUpload();
  const [financeSendAllStudentMessage] = useFinanceSendAllStudentMessage();
  const [financeSendMesageStudentList] = useFinanceSendMesageStudentList();
  const [financeSendMesageAcademicStudentList] =
    useFinanceSendMesageAcademicStudentList();
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setFile({
            name: e.target.files[0]?.name,
            key: res.data?.imageKey,
            type: e.target.files[0]?.type,
          });
          setSendNotify((prev) => ({
            ...prev,
            m_doc: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onGetStudentList = (flow) => {
    if (flow === "Normal") {
      if (instituteId) {
        setDisabled((pre) => !pre);
        financeSendMesageStudentList({
          id: instituteId,
          filterData: {
            depart: applyFilter?.depart?._id ?? "",
            batch: applyFilter?.batch?._id ?? "",
            master: applyFilter?.master?._id ? [applyFilter?.master?._id] : [],
            batch_status: applyFilter?.batch_status ?? "",
            all_depart: applyFilter?.all_depart ?? "",
            category: applyFilter?.category ?? "",
          },
        })
          .then((res) => {
            setStudentList(res?.data?.all_student);
            setDisabled((pre) => !pre);
            setStep(2);
          })
          .catch({});
      }
    } else if (flow === "Academic") {
      if (applyFilter?.master?._id) {
        setDisabled((pre) => !pre);
        financeSendMesageAcademicStudentList({
          cid: applyFilter?.master?._id,
          did: applyFilter?.depart?._id,
        })
          .then((res) => {
            setStudentList(res?.data?.all_students);
            setDisabled((pre) => !pre);
            setStep(2);
          })
          .catch({});
      }
    } else {
    }
  };

  const onGrabStudent = (val) => {
    setSelectedId(val);
    setStep("");
  };

  const onAction = () => {
    let validation = requiredField(sendNotify, ["content", "m_doc"]);
    if (existingRequiredField(validation)) {
      if (sendNotify.alarm_mode && selectedId?.length > 0 && instituteId) {
        setDisabled((pre) => !pre);
        financeSendAllStudentMessage({
          sendMessage: {
            message: sendNotify.content,
            from: headId ?? "",
            type: accessAs ?? "Institute Admin",
            // alarm_mode: sendNotify.alarm_mode,
            filtered_arr: selectedId,
            flow: accessAs === "INSTITUTE_ADMIN" ? "INSTITUTE_ADMIN" : "Other",
            m_title: sendNotify.m_title ?? "",
            m_doc: sendNotify.m_doc ?? null,
            status: sendNotify.alarm_mode,
          },
          id: instituteId,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  // console.info("instituteAllStudentIdList", applyFilter);
  return (
    <>
      {step === 1 ? (
        <PendingFeesSendReminderFilter
          applyFilter={applyFilter}
          setApplyFilter={setApplyFilter}
          onClose={onClose}
          fid={financeId}
          onActionEventFire={onGetStudentList}
          instituteId={instituteId}
        />
      ) : step === 2 ? (
        <PendingFeesSendReminderStudentList
          alreadySelected={selectedId}
          studentList={studentList}
          onAction={onGrabStudent}
          onClose={() => setStep(1)}
        />
      ) : (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("student_message")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <section
                className={style.notify_container}
                onClick={() =>
                  setSendNotify((prev) => ({
                    ...prev,
                    alarm_mode: "APP_NOTIFICATION",
                  }))
                }
              >
                <img
                  src={
                    sendNotify.alarm_mode === "APP_NOTIFICATION"
                      ? `${assestsUtilityUrl}/radio-blue.svg`
                      : `${assestsUtilityUrl}/radio-grey.svg`
                  }
                  alt="icon"
                />
                <div className={style.notify_text}>
                  <h6>{t("Notify_with_In_app_Notification")}</h6>
                  <p>{t("Notify_with_In_app_Notification_note")}</p>
                </div>
              </section>
              <section
                className={style.notify_container}
                onClick={() =>
                  setSendNotify((prev) => ({
                    ...prev,
                    alarm_mode: "EMAIL_NOTIFICATION",
                  }))
                }
              >
                <img
                  src={
                    sendNotify.alarm_mode === "EMAIL_NOTIFICATION"
                      ? `${assestsUtilityUrl}/radio-blue.svg`
                      : `${assestsUtilityUrl}/radio-grey.svg`
                  }
                  alt="icon"
                />
                <div className={style.notify_text}>
                  <h6>{t("Notify_On_Email_and_SMS")}</h6>
                  <p>{t("Notify_On_Email_and_SMS_note")}</p>
                </div>
              </section>
              {/* {sendNotify.alarm_mode === "APP_NOTIFICATION" && (
                <> */}
              <FormTextarea
                label={t("student_title_label")}
                placeholder={t("student_title_label_placeholder")}
                name="m_title"
                value={sendNotify.m_title}
                type="text"
                onChange={(e) =>
                  setSendNotify((prev) => ({
                    ...prev,
                    m_title: e.target.value,
                  }))
                }
                errorShow={errorField["m_title"]}
              />
              <FormTextarea
                label={t("student_message_label")}
                placeholder={t("student_message_label_placeholder")}
                name="content"
                value={sendNotify.content}
                type="text"
                onChange={(e) =>
                  setSendNotify((prev) => ({
                    ...prev,
                    content: e.target.value,
                  }))
                }
              />
              <FormFile
                label={t("attachment")}
                name={"document"}
                value={file}
                onChange={onFileUpload}
              />
              {/* </>
              )} */}
              <CreateButton label="confirm" onAction={onAction} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AllStudentMessageSend;

